.footer {
    background-color: var(--white-color);
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.footer__container {
    padding: 2rem 0 4rem;
}

.footer__title,
.footer__link {
    color: var(--title-color);
}

.footer__title {
    text-align: center;
    margin-bottom: var(--mb-2);
}

.footer__link:hover {
    color: var(--title-color-dark);
}

.footer__list {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 1.5rem;
    padding: 0;
    margin: 0;
    margin-bottom: 1rem;
}

.footer__social {
    display: flex;
    justify-content: center;
    column-gap: 1rem;
}

.footer__social-link {
    font-size: 1.2rem;
    color: var(--title-color);
    padding: 0.4rem;
    border-radius: 0.5rem;
}

.footer__social-link:hover {
    color: var(--title-color-dark);
}

.footer__copy {
    display: block;
    margin-top: 4.5rem;
    font-size: var(--smaller-font-size);
    text-align: center;
}

